}

#staplesbmincart form ul {
    overflow-y: scroll;
    max-height: 300px;
    border-radius: 6px;
}

#staplesbmincart {
    /* display: none; */
    position: fixed;
    right: 2%;
    top: 16%;
    width: 420px;
}

.sbmincart-showing #staplesbmincart {
    display: block;
    z-index: 9999;
    -webkit-animation: zoomIn .3s ease;
    animation: zoomIn .3s ease;
}

#staplesbmincart form.sbmincart-empty {
    font-size: 16px;
    font-weight: bold;
}

