    clear: both;
    float: left;
    width: 100%;
    margin: 5px 0 20px;
    padding: 1em;
    list-style-type: none;
    background: #f5f0f0;
    border: none;
    border-radius: 0px;
    box-shadow: none;
}

#staplesbmincart .sbmincart-empty ul {
    display: none;
}

#staplesbmincart .sbmincart-closer {
    float: right;
    margin: -8px -10px 0;
    padding: 0 10px;
    background: 0;
    border: 0;
    font-size: 22px;
    cursor: pointer;
    font-weight: bold;
    outline: none;
}

#staplesbmincart .sbmincart-closer:hover {
    opacity: 0.8;
}

#staplesbmincart .sbmincart-item {
    clear: left;
    display: grid;
    align-items: center;
    grid-template-columns: 8fr 1fr 1fr 3fr;
    grid-gap: 10px;
    margin-bottom: 10px;
    margin-bottom: 10px;
}

#staplesbmincart .sbmincart-item + .sbmincart-item {
    border-top: 1px solid #f2f2f2;
}

#staplesbmincart .sbmincart-item a {
    color: #212529;
    text-decoration: none;
    font-weight: 600;
    font-size: 15px;
}

.sbmincart-details-quantity {
    display: grid;
    align-items: center;
}

#staplesbmincart .sbmincart-details-remove {
    text-align: center;
}

#staplesbmincart .sbmincart-details-price {
    text-align: center;
    font-size: 15px;
    color: #232020;
    font-weight: 600;
}

#staplesbmincart .sbmincart-attributes {
    margin: 0;
    padding: 0;
    background: transparent;
    border: 0;
    border-radius: 0;
    box-shadow: none;
    color: #999;
    font-size: 12px;
    line-height: 22px;
    overflow: inherit;
    height: inherit;
}

#staplesbmincart .sbmincart-attributes li {
    display: inline;
}

#staplesbmincart .sbmincart-attributes li:after {
    content: ",";
}

#staplesbmincart .sbmincart-attributes li:last-child:after {
    content: "";
}

#staplesbmincart .sbmincart-quantity {
    width: 30px;
    height: 20px;
    padding: 2px 4px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    font-size: 12px;
    text-align: right;
    transition: border linear 0.2s, box-shadow linear 0.2s;
    -webkit-transition: border linear 0.2s, box-shadow linear 0.2s;
    -moz-transition: border linear 0.2s, box-shadow linear 0.2s;
}

#staplesbmincart .sbmincart-quantity:hover {
    border-color: var(--primary-color);
}

#staplesbmincart .sbmincart-quantity:focus {
    border-color: var(--primary-color);
    outline: 0;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 3px rgba(0, 120, 193, 0.4);
}

#staplesbmincart .sbmincart-remove {
    width: 20px;
    height: 20px;
    margin: 0;
    padding: 0;
    background: var(--primary-color);
    border: 1px solid var(--primary-color);
    border-radius: 3px;
    color: #fff;
    font-size: 13px;
    opacity: 0.70;
    cursor: pointer;
    line-height: 14px;
    display: inline-block;
}

#staplesbmincart .sbmincart-remove:hover {
    opacity: 1;
}

#staplesbmincart .sbmincart-footer {
    clear: left;
    text-align: center;
    margin-right: 0;
    position: relative;
}

p.sbmincart-empty-text {
    color: #f44336;
    font-weight: 500;
}

#staplesbmincart .sbmincart-subtotal {
    bottom: 3px;
    padding-left: 0;
    font-size: 15px;
    font-weight: bold;
    display: block;
    text-align: left;
}

#staplesbmincart .sbmincart-submit {
    margin-right: 0px;
    padding: 0;
    border: none;
    color: var(--primary-color);
    background: none;
    outline: none;
    font-size: 12px;
    font-weight: 600;
    position: absolute;
    text-transform: uppercase;
    right: 0;
    top: 0;
    cursor: pointer;
}

#staplesbmincart .sbmincart-submit:hover {
    opacity: 0.8;
}

/* Let's get this party started */
