    border-radius: var(--border-radius);
}

.w3l-faq-sec .faq-page details p {
    color: var(--font-color);
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: 15px;
    line-height: 28px;
    font-weight: normal;
}

.w3l-faq-sec .faq-page summary {
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    font-size: 19px;
    line-height: 25px;
    font-weight: 600;

    color: var(--heading-color);

}

.w3l-faq-sec .faq-page summary:focus {
    outline: none;
}

.w3l-faq-sec .faq-page summary::-webkit-details-marker {
    display: none;
}

.w3l-faq-sec .faq-page .control-icon {
    fill: rebeccapurple;
    transition: .3s ease;
    pointer-events: none;
}

.w3l-faq-sec .faq-page .control-icon-close {
    display: none;
}

.w3l-faq-sec .faq-page details[open] .control-icon-close {
    display: initial;
    transition: .3s ease;
}

.w3l-faq-sec .faq-page details[open] .control-icon-expand {
    display: none;
}

.w3l-faq-sec .faq-page details[open] summary:hover::after {
    -webkit-animation: pulse 1s ease;
    animation: pulse 1s ease;
}

@-webkit-keyframes pulse {
    25% {
        transform: scale(1.1);
    }

    50% {
        transform: scale(1);
    }

    75% {
        transform: scale(1.1);
    }

    100% {
        transform: scale(1);
    }
}

@keyframes pulse {
    25% {
        transform: scale(1.1);
    }

    50% {
        transform: scale(1);
    }

    75% {
        transform: scale(1.1);
    }

    100% {
        transform: scale(1);
    }
}

